import React, { Component } from 'react';
import { findDOMNode } from 'react-dom';
import PropTypes from 'prop-types';
import { Row, Col } from 'antd';
import _ from 'lodash';
import { hexToRgb } from '../utils/common';
import classnames from 'classnames';
import PALLET from './constants';
import PronounRelevanceStore from './PronounRelevanceStore';
import PronounRelevanceBoard from './PronounRelevanceBoard';
import { PrBoardCompareProps, PrBoardCompareState } from './interface';

class PronounRelevanceCompareBoard extends Component<PrBoardCompareProps, PrBoardCompareState> {
  falsy: any;
  store: any;
  left: any;
  right: any;
  
  constructor(props: PrBoardCompareProps) {
    super(props);

    this.falsy = {};
    this.store = {};

    this.state = {
      falsy: null,
      entityIndex: null,
      gtHighlightEntities: [],
      uaHighlightEntities: [],
    };
  }

  static propTypes = {
    userAnswer: PropTypes.instanceOf(PronounRelevanceStore),
    gtAnswer: PropTypes.instanceOf(PronounRelevanceStore),
  };

  componentWillReceiveProps(nextProps: PrBoardCompareProps) {
    const { userAnswer, gtAnswer } = nextProps;
    if (
      userAnswer !== this.props.userAnswer ||
      gtAnswer !== this.props.gtAnswer
    ) {
      this.init(nextProps);
      this.forceUpdate();
    }
  }

  componentDidMount() {
    this.init(this.props);
    this.forceUpdate();
  }

  componentWillUpdate(nextProps: PrBoardCompareProps, nextState: PrBoardCompareState) {
    if (!this.left || !this.right) return;
    this.left.clearHeight();
    this.right.clearHeight();
  }

  componentDidUpdate(prevProps: PrBoardCompareProps, prevState: PrBoardCompareState) {
    if (!this.left || !this.right) return;
    const heightGroupRight = this.right.heightGroup;
    const heightGroupLeft = this.left.heightGroup;
    this.setHeight(heightGroupLeft, heightGroupRight);
  }

  setHeight = (heightGroupLeft: object, heightGroupRight: object) => {
    for (const pair in heightGroupLeft) {
      if (!heightGroupRight[pair]) continue;

      const left: any = findDOMNode(heightGroupLeft[pair]);
      const right: any = findDOMNode(heightGroupRight[pair]);

      const max = Math.max(left.clientHeight, right.clientHeight);
      left.style.height = `${max}px`;
      right.style.height = `${max}px`;
    }
  };

  init = (props: PrBoardCompareProps) => {
    const { userAnswer, gtAnswer } = props;
    if (!userAnswer || !gtAnswer) return;
    const [match_gt, match_ua] = gtAnswer.generateMatch(userAnswer.entities);

    this.falsy = {
      negative: gtAnswer.generateFalsy(userAnswer.entities),
      positive: userAnswer.generateFalsy(gtAnswer.entities),
      match: match_gt,
      match_ua,
    };

    this.store = {
      negative: gtAnswer,
      positive: userAnswer,
      match: gtAnswer,
    };
  };

  onSelectEntity = (falsy: string | null, entityIndex: number) => {
    let uaHighlightEntities = [];
    let gtHighlightEntities = [];
    const { positive, negative } = this.store;
    switch (falsy) {
      case 'negative': {
        gtHighlightEntities.push(entityIndex);
        uaHighlightEntities = positive.getIntersectionEntities(
          negative.entities[entityIndex],
        );
        break;
      }
      case 'positive': {
        uaHighlightEntities.push(entityIndex);
        gtHighlightEntities = negative.getIntersectionEntities(
          positive.entities[entityIndex],
        );
        break;
      }
      case 'match': {
        const { match, match_ua } = this.falsy;
        gtHighlightEntities.push(entityIndex);
        const index = match.indexOf(entityIndex);
        uaHighlightEntities.push(match_ua[index]);
        break;
      }
    }

    this.setState({
      entityIndex,
      falsy,
      uaHighlightEntities,
      gtHighlightEntities,
    });
  };

  renderWords = (falsy: string, entityIndex: number) => {
    const store = this.store[falsy];
    const [sentenceId, start, end] = _.first(store.entities[entityIndex]);
    const words = store.sentences[sentenceId].slice(start, end);
    const style: any = {};

    if (store.entities[entityIndex] && store.entities[entityIndex].length > 1) {
      style.borderColor = PALLET[entityIndex % PALLET.length];
    }

    if (this.state.falsy === falsy && this.state.entityIndex === entityIndex) {
      style.backgroundColor = style.borderColor
        ? hexToRgb(style.borderColor, 0.3)
        : 'rgba(217, 217, 217, .4)';
    }

    return (
      <div
        className={classnames('group-wrapper')}
        style={style}
        onClick={() => this.onSelectEntity(falsy, entityIndex)}
      >
        {words.join(' ')}
        <span className="subscript">{entityIndex}</span>
      </div>
    );
  };

  renderFalsy = (falsyName: string) => {
    const falsy = this.falsy[falsyName];
    return (
      falsy &&
      falsy.map((entityId: number, index: number) => (
        <li key={index}>{this.renderWords(falsyName, entityId)}</li>
      ))
    );
  };

  render() {
    const { userAnswer, gtAnswer, showRealGroundTruth } = this.props;
    const {
      uaHighlightEntities,
      gtHighlightEntities,
    } = this.state;
    const compareText = showRealGroundTruth ? 'GT ANSWER:' : 'REVIEW ANSWER:';
    return (
      <Row className="pronoun-relevance compare-board">
        <Col span={6} className="compare-board-column">
          <section className="falsy-section">
            <h3>False Negative 漏标</h3>
            <ul>{this.renderFalsy('negative')}</ul>
          </section>
          <section className="falsy-section">
            <h3>False Positive 多标</h3>
            <ul>{this.renderFalsy('positive')}</ul>
          </section>
          <section className="falsy-section">
            <h3>Match 完全相同</h3>
            <ul>{this.renderFalsy('match')}</ul>
          </section>
        </Col>
        <Col span={18} className="compare-board-column">
          <Row className="compare-area">
            <Col span={12}>
              <h3>{compareText}</h3>
              <PronounRelevanceBoard
                ref={(left: any) => this.left = left}
                store={gtAnswer}
                isControllable={false}
                isReviewing
                relatedEntities={gtHighlightEntities}
              />
            </Col>
            <Col span={12}>
              <h3>USER ANSWER:</h3>
              <PronounRelevanceBoard
                ref={(right: any) => this.right = right}
                store={userAnswer}
                isControllable={false}
                isReviewing
                relatedEntities={uaHighlightEntities}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }
}

export default PronounRelevanceCompareBoard;
