import { Button } from 'antd';
import { Pagination } from 'antd';
import { message } from 'antd';
import * as React from "react";
import _ from 'lodash';
import './PreviewOnline.css';
import PronounRelevance from './PronounRelevance/App';
import fileService from '../services/fileService';
import update from 'immutability-helper';
import constants from '../constants';

const { FILE_NAMES } = constants;
export interface Props{
}

interface State {
    currentFileNum: number,
    currentTextNum: number,
    currentTextContent: string,
    total: number,
}

class Intro extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.state = {
            currentFileNum: 0,
            currentTextNum: 1,
            currentTextContent: "",
            total: 0,
        };
    }

    componentDidMount() {
        this.switchFile(0);
    }

    fetchPassage = (fileName: string, passageId: string) => {
        const { currentTextContent } = this.state;
        fileService.getFileContent(fileName, passageId).then(res => {
            if (res.rtn === 0) {
                this.setState({
                    currentTextContent: update(currentTextContent, { $set: res.result}),
                });
            }
            else {
                message.error("Failed to fetch passage");
            }
        })
    }

    switchFile = (fileNum: number) => {
        fileService.getPassageCount(FILE_NAMES[fileNum]).then(res => {
            if(res.rtn === 0) {
                this.setState({
                   total: res.total
                })
            }
            else {
                message.error("Failed to fetch passage");
            }
        })
        this.fetchPassage(FILE_NAMES[fileNum], "1");
        this.setState({
            currentFileNum: fileNum,
            currentTextNum: 1
        });
    }

    onPageChange = (pageNum: number) => {
        const { currentFileNum } = this.state;
        this.fetchPassage(FILE_NAMES[currentFileNum.toString()], pageNum.toString());
        this.setState({
            currentTextNum: pageNum
        })
    }

    public render() {
      const { currentTextContent, currentTextNum, currentFileNum, total } = this.state;
      const content = JSON.parse(currentTextContent || "{}");
      return (
        <div className="preview-online">
          <div className="left-tab">
            <Button key="0" onClick={(e: any) => this.switchFile(0)} className="set-button" type={currentFileNum === 0 ? "primary" : "default"}>Training Set (37000)</Button>
            <br />
            <Button key="1" onClick={(e: any) => this.switchFile(1)} className="set-button" type={currentFileNum === 1 ? "primary" : "default"}>Dev Set (500)</Button>
            <br />
            <Button key="2" onClick={(e: any) => this.switchFile(2)} className="set-button" type={currentFileNum === 2 ? "primary" : "default"}>Test Set (500)</Button>
          </div>
          <div className="pagination" >
            <Pagination showQuickJumper current={currentTextNum} total={total} pageSize={1} onChange={this.onPageChange} />
          </div>
          <div className="pronoun-relevance-content">
            {
                !_.isEmpty(content) ? <PronounRelevance taskFile={{sentence: content.sentences || []}} annoResult={content.mention_clusters || []} />
                : <div className="empty-passage" >Empty Passage</div>
            }
          </div>
        </div>
      );
    }
  }

export default Intro;
