import _ from 'lodash';
import * as React from 'react';
import { Col, Row } from 'antd';
import './App.css';
import { PrProps } from './PronounRelevance/interface';
import PronounRelevanceBoard from './PronounRelevance/PronounRelevanceBoard';
import PronounRelevanceCompareBoard from './PronounRelevance/PronounRelevanceCompareBoard';
import PronounRelevanceStore from './PronounRelevance/PronounRelevanceStore';

export const isResultValid = (result: object) => {
  return Array.isArray(result);
}

class App extends React.Component<PrProps> {
  public _store: any;
  public _annoStore: any;
  public _gtStore: any;

  static defaultProps = {
    globalControl: {
      "isEditable": false,
      "showGroundTruth": false,
      "showHeader": false,
      "showRealGroundTruth": false,
      "updateTarget": "anno"
    }
  }

  constructor(props: PrProps) {
    super(props);

    this._store = null;
    this.init(props);
  }

  componentWillReceiveProps(nextProps: PrProps) {
    this.init(nextProps);
  }

  _forceUpdate = (payload?: any) => {
    if(this.props.onChange) this.props.onChange(this._annoStore.entities) ;
  };

  init = (props: PrProps) => {
    const { globalControl, taskFile, annoResult } = props;

    this._annoStore = new PronounRelevanceStore(taskFile.sentence, annoResult);
    this._store = this._annoStore;

    if (globalControl.showGroundTruth) {
      const { gtResult } = props.taskFile;
      this._gtStore = new PronounRelevanceStore(taskFile.sentence, gtResult);
    }
  };

  render() {
    const { globalControl } = this.props;
    const isAnnoMode = globalControl.isEditable;
    if (globalControl.showGroundTruth) {
      return (
        <PronounRelevanceCompareBoard
          userAnswer={this._annoStore}
          gtAnswer={this._gtStore}
          showRealGroundTruth={globalControl.showRealGroundTruth}
        />
      );
    }
    return (
      <Row className="pronoun-relevance">
        <Col style={{ height: '100%' }} span={isAnnoMode ? 24 : 0}>
          <PronounRelevanceBoard
            store={this._store}
            isControllable
            forceUpdate={this._forceUpdate}
          />
        </Col>
        <Col style={{ height: '100%' }} span={isAnnoMode ? 0 : 24}>
          {!isAnnoMode && (
            <div style={{ height: '100%' }}>
              {globalControl.showHeader && '标注员答案: '}
              {
                <PronounRelevanceBoard
                  store={this._annoStore}
                  isControllable={false}
                />
              }
            </div>
          )}
        </Col>
      </Row>
    );
  }
}

export default App;
