import axios from 'axios';
import constants from '../constants';

const { API_URL } = constants;

class FileService {
    getFileContent = (fileName: string, passageId: string) => {
        return axios.get(`${API_URL}api/file?name=${fileName}&id=${passageId}`)
        .then(res => {
            return res.data;
        })
    }

    getPassageCount = (fileName: string) => {
        return axios.get(`${API_URL}api/file/passageCount?name=${fileName}`)
        .then(res => {
            return res.data;
        })
    }
};

export default new FileService();
