import { Button } from 'antd';
import { Form } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { Icon } from 'antd';
import { Input } from 'antd';
import { Modal } from 'antd';
import { message } from 'antd';
import * as React from "react";
import _ from 'lodash';

import constants from "../constants";
import emailService from '../services/emailService';

import "./Intro.css";
const FormItem = Form.Item;
const { PRECO_INTRO, PAPER_INTRO, PAPER_AUTHOR, DATA_FORMAT, COPYRIGHT_INFO, MODAL_TITLE } = constants;

export interface Props extends FormComponentProps {
    onChangeTab(key: string): void;
}

interface State {
    visible: boolean;
}

function hasErrors(fieldsError: object) {
    return Object.keys(fieldsError).some(field => fieldsError[field]);
}

class Intro extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.state = {
            visible: false,
        };
    }

    handleSubmit = (e: any) => {
        e.preventDefault();
        this.props.form.validateFields((err: any, values: object) => {
            if (!err) {
                this.setState({
                    visible: true,
                });
            }
        })
    }

    public changeTab = (e: any) => {
        const { onChangeTab } = this.props;
        onChangeTab("2");
    }

    public handleOk = () => {
        this.props.form.validateFields((err: any, values: object) => {
            emailService.sendEmail(values).then(res => {
                if (res.rtn === 0) {
                    message.info("The download link has been sent to your email");
                }
                else {
                    message.error("Oops, something went wrong");
                }
            });
        });
        this.setState({
            visible: false,
        });
    }

    public closeModal = () => {
        this.setState({
            visible: false,
        });
    }

    public render() {
      const { getFieldDecorator, getFieldsError } = this.props.form;
      return (
        <div className="intro">
          <div className="header">
            <h1>What is PreCo?</h1>
            <p>{PRECO_INTRO.p1}</p>
            <p>{PRECO_INTRO.p2}</p>
            <h1>Paper</h1>
            <p>{PAPER_INTRO}</p>
            <b>{PAPER_AUTHOR}</b>
            <div className="info">
              <h1>Download</h1>
              <Form layout="inline" onSubmit={this.handleSubmit}>
                <FormItem>
                  {getFieldDecorator("name", {
                      rules: [{required: true, message: "Please input your name"}],
                  })(<Input prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />} placeholder="Name" />)}
                </FormItem>
                <FormItem>
                  {getFieldDecorator("institute", {
                      rules: [{required: true, message: "Please input your institute"}],
                  })(<Input prefix={<Icon type="home" style={{ color: "rgba(0,0,0,.25)" }} />} placeholder="Institute" />)}
                </FormItem>
                <FormItem>
                  {getFieldDecorator("email", {
                      rules: [{ type: 'email', message: 'The input is not valid E-mail!'}, {required: true, message: 'Please input your E-mail'}],
                  })(<Input prefix={<Icon type="mail" style={{ color: "rgba(0,0,0,.25)" }} />} placeholder="E-mail" />)}
                </FormItem>
                <br />
                <FormItem style={{textAlign: 'center'}}>
                  <Button type="primary" htmlType="submit" className="send-button" disabled={hasErrors(getFieldsError())}
                  >Send</Button>
                </FormItem>
              </Form>
              <Modal
                className="modal"
                visible={this.state.visible}
                onCancel={this.closeModal}
                title={MODAL_TITLE}
                footer={[<Button key="ok" onClick={this.handleOk} type="primary">I agree with the above note</Button>]}
              >
                {
                    _.map(_.keys(COPYRIGHT_INFO), key => {
                        return <p>{COPYRIGHT_INFO[key]}</p>
                    })
                }
            </Modal>
            </div>
            <h1>Data Format</h1>
            <p>{DATA_FORMAT.summary}</p>
            <ul>
              <li>{DATA_FORMAT.l1}</li>
              <li>{DATA_FORMAT.l2}</li>
              <li>"mention_clusters": the mention clusters of the document. It is a list of mention clusters. Each mention cluster is a list of mentions. Each mention is a tuple of integers [sentence_idx, begin_idx, end_idx]. Sentence_idx is the index of the sentence of the mention. Begin_idx is the index of the first token of the mention in the sentence. End_index is the index of the last token of the mention in the sentence <b>plus one</b>. All indices are <b>zero-based</b>.</li>
            </ul>
            <Button type="primary" className="preview-button" onClick={this.changeTab}>Preview Online</Button>
            <h1>
                Contact
            </h1>
            <p>Please contact <b><a href="mailto:preco@yitu-inc.com">preco@yitu-inc.com</a></b> for questions about the dataset.</p>
          </div>
        </div>
      );
    }
  }

export default Form.create<Props>()(Intro);
