import * as React from 'react';
import { Tabs } from 'antd';
import './App.css';
import Intro from './components/Intro';
import PreviewOnline from './components/PreviewOnline';
const { TabPane } = Tabs;

export interface Props {

}
interface State {
  currentTab: string;
}
class App extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);
    this.state = { currentTab: "1"};
  }

  changeTab = (key: string) => {
    this.setState({
      currentTab: key,
    });
  }

  public render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title"><a href="/">PreCo</a></h1>
        </header>
        <div className="tabs">
            <Tabs activeKey={this.state.currentTab} onChange={this.changeTab}>
              <TabPane tab="Home" key="1">
                <Intro onChangeTab={this.changeTab}/>
              </TabPane>
              <TabPane tab="Preview Online" key="2">
                <PreviewOnline />
              </TabPane>
            </Tabs>
          </div>
        <footer className="App-footer">
          <p>Copyright©2018 by PreCo Dataset. All rights reserved.</p>
        </footer>
      </div>
    );
  }
}

export default App;
